body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container{
  max-width: 1170px;
  margin: auto;
}
.navbar{
  padding: 1rem;
  background: #171717;
  position: sticky;
  top: 0;
}
.navbar__nav{
 display: flex;
 justify-content: flex-start;
 align-items: center;
}
.nav_brand a{
 font-size: 1.4rem;
  color: white;
  text-decoration: none;
}
.nav_brand a:hover{
  color: #e10092;
}
.nav__links{
  display: flex;
  list-style: none;
  align-items: center;
}
.links_link a{
 display: block;
 padding: 10px;
 color: white;
 justify-content: space-around;
 text-decoration: none;
 margin: left 0.5rem;
 border-bottom: 1px solid transparent;
 font-size: 20px;
}
.links_link a:hover{
  border-bottom: 1px solid white;
  color: #e10092;

}

.nav__icon{
  display: flex;
  list-style: none;
  align-items: flex-end;
  color: white;

}
.body_container{
  background-color: black;
    height: 80vh;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
}
.image_grid{
  display: inline-flex;
  flex-direction: row;
  flex-wrap: wrap;

}
.image_grid_list{
  list-style: none;
  border: 4px solid transparent;
}
.image_grid_list:hover{
  border: 4px solid #e10092;
}
.storecontainer{
  background: black;
}
@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loading-spinner {
  width: 150px;
  height: 150px;
  border: 10px solid #f3f3f3; /* Light grey */
  border-top: 10px solid #e10092; /* Blue */
  border-radius: 50%;
  animation: spinner 1s linear infinite;
}

.spinner-container {
  display: grid;
  justify-content: center;
  align-items: center;
  height: 550px;
}
